<template>
  <div class="header">
    <div class="container">
      <canvas id="scene">
        <div class="header__content" @click="GLTFloader()">
          <h1 class="header__content-title">YTONG</h1>
        </div>
      </canvas>
    </div>
  </div>
</template>

<script>
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";

export default {
  name: "ThreeTest",
  data() {
    return {
      camera: null,
      scene: null,
      renderer: null,
      mesh: null,
    };
  },
  methods: {
    init: function () {
      var loader = new GLTFLoader();

      loader.load(
        "https://github.com/JaneDann/yt/rocket.gltf",
        function (gltf) {
          this.scene.add(gltf.scene);
        },
        undefined,
        function (error) {
          console.error(error);
        }
      );
    },
    animate: function () {},
    onWindowResize: function () {
      this.camera.aspect = window.innerWidth / window.innerHeight;
      this.camera.updateProjectionMatrix();

      this.renderer.setSize(window.innerWidth, window.innerHeight);
    },
  },
  mounted() {
    this.init();
    this.animate();
  },
};
</script>


<style lang="scss">
@import "@/assets/scss/grid/grid";
#canvas {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.header {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  background: #fdb813;
  height: 120vh;

  &__content {
    width: 100%;
    height: 120vh;
    display: flex;
    justify-content: center;
    align-items: center;
    &-title {
      @include adaptiv-fontlg(375, 280);
      @include adaptiv-lineHeightlg(375, 280);
      font-family: "dinpro";
      color: #231f20;
    }
  }
}
</style>